html {
	background: #272727;
}

body {
	background: #272727;
}


.phote {
	display: inline-block;
    border-radius: 20px;
    width: 40%;
    height: auto;
    margin-top: 10px;
}

.photeglav {
	background-image: url(../img/hh.jpg);
	background-size: cover;
    width: 100%;
    height: 400px;
    background-position: center;
}

.button {
	display: inline-block;
	font-size: 15px;
	background-color: #454647;
	font-weight: 400;
	margin-top: 10px;
	padding: 12px;
	text-decoration: none;
	color: #fff;
	width: 91%;
	border-radius: 10px;
	text-align: center;
	cursor: pointer;
	transition: 0.5s ease;
}
@keyframes god {
	100% {
		   box-shadow: 0 0 0px #ccc;
	}
	50% {
		    box-shadow: 0 0 30px #ccc;
	}
	0% {
		    box-shadow: 0 0 0px #ccc;
	}
}
.button22 {
	display: inline-block;
	font-size: 15px;
	background-color: #007bff;
	font-weight: 400;
	padding: 12px;
	text-decoration: none;
	color: #fff;
	width: 100%;
	bottom: 0%;
	border-top-right-radius: 10px;
	border-top-left-radius: 10px;
	text-align: center;
	cursor: pointer;
	transition: 0.5s ease;
	position: fixed;
	z-index: 1000;
	padding-left: 10px;
    text-transform: uppercase;
    animation: god 2s ease infinite;
}


