* {
  box-sizing: border-box;
}


html {
	overflow-x: hidden;
}

body {
    font-family: 'Arial';
    background-color: #272727;
	margin: 0;
  	box-sizing: border-box;
  	overflow-x: hidden;
}


.headerd {
	display: flex;
    z-index: 10000;
    background-color: #272727;
    height: 55px;
    border: none;
    margin-bottom: 0px;
    width: 100%;
    padding: 0px;
}

.teds {
    display: block;
    margin: 0 auto;
    margin-top: 0px;
    margin-bottom: 0px;
    font-size: 1.2rem;
    color: #fff;
    font-weight: 500;
    line-height: 55px;
    text-align: left;
    margin-left: 26px;
}

.lined {
    background-color: #2e2e2e;
    width: 92%;
    margin: 0 auto;
    height: 1px;
    margin-bottom: 20px;
}

.main {
    background-color: #272727;
}

.obl {
    background-image: url(../img/obl.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%;
    filter: blur(10px);
    position: fixed;
    z-index: 3000;
}

.back {
    margin-top: -18px;
    color: #3f8ae0;
    cursor: pointer;
}
.d {
    display: contents;
    padding: 0px;
    margin: 0px;
}
#inspiration {
  position: fixed;
  right: 1em;
  bottom: 1em;
}
#inspiration a {
  display: inline-block;
  float: left;
  text-decoration: none;
  font-weight: bold;
  color: white;
  transition: all 1s ease;
}
#inspiration a:hover {
  color: #212121;
}
#inspiration p {
  margin: 0;
  padding-left: 0.4em;
  display: inline-block;
  float: right;
  color: rgba(255, 255, 255, 0.6);
}
#pin {
  width: 20em;
  display: flex;
  min-width: 534px;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 1em;
  border-radius: 0.3em;
  margin: auto;
  color: rgba(255, 255, 255, 0.2);
  transition: 0.5s ease;
}
.dots {
  width: 50%;
  display: flex;
  justify-content: space-around;
  padding: 1em;
  padding-top: 2em;
}
.dot {
  position: relative;
  background: #fff;
  border-radius: 0.8em;
  width: 0.8em;
  height: 0.8em;
  transform: scale3d(0.7, 0.7, 0.7);
}
.dot.active {
  animation: growDot 0.5s ease;
  animation-fill-mode: forwards;
  background: green;
}
.dot.wrong {
  animation: wrong 0.9s ease;
}
.dot.correct {
  animation: correct 0.9s ease;
}
#pin p {
  font-size: 0.8em;
  color: #fff;
}
.numbers {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-around;
  align-content: flex-end;
  margin-top: 1em;
  margin-bottom: 1em;
}
.number {
  width: 2.5em;
  height: 2.5em;
  margin: 0.5em;
  border-radius: 2.5em;
  text-align: center;
  line-height: 2.7em;
  font-weight: 500;
  font-size: 1.8em;
  user-select: none;
  color: #fff;
  transition: all 0.5s ease;
}
.number:hover {
    background: #cccccc5e;
    cursor: pointer;
  color: #000;
}

.number:before {
  content: "";
  position: absolute;
  left: -2px;
  width: 2.5em;
  height: 2.5em;
  border-radius: 2.5em;
  transition: all 0.5s ease;
}

@keyframes growDot {
  100% {
    background: green;
    transform: scale3d(0.9, 0.9, 0.9);
  }
}

@keyframes wrong {
  20% {
    background: crimson;
  }
  40% {
    transform: translate(-15px, 0);
  }
  60% {
    transform: translate(10px, 0);
  }
  80% {
    transform: translate(-5px, 0);
  }
}
@keyframes correct {
  20% {
    background: limegreen;
  }
  40% {
    transform: translate(0, -15px);
  }
  60% {
    transform: translate(0, 10px);
  }
  80% {
    transform: translate(0, -5px);
  }
}
@keyframes bg-red {
  50% {
    background: crimson;
  }
}
@keyframes bg-green {
  50% {
    background: limegreen;
  }
}
@media(max-width: 372px) {
    #pin {
        width: 100%;
        margin: 0px auto;
    }
    .number {
        font-size: 1.5em;
        line-height: 2.7em;
    }
}

@media(max-width: 314px) {
    .number {
        font-size: 1.3em;
        line-height: 2.9em;
    }
}

@media(max-width: 278px) {
    .number {
        font-size: 1.1em;
        line-height: 2.9em;
    }
}
@keyframes donut-spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.donut {
  display: inline-block;
  border: 2.5px solid #fff;
  border-left-color: #0d6efd;
  border-right-color: #0d6efd;
  border-top-color: #0d6efd;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  animation: donut-spin .7s linear infinite;
}
.read {
    z-index: 10000;
    position: fixed;
    width: 40em;
    height: 500px;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%) scale(0);
    display: none;
    opacity: 0;
    transition: 0.5s ease;
}

.beg {
    margin-top: 0;
    letter-spacing: 1;
    color: green;
    opacity: .8;
    transition: 0.5s ease;
}

.vd {
    transition: 0.5s ease;
     text-align: center;
      display: none;
}

@media (max-width: 474px) {
    .gl {
        width: 300px;
        height: auto;
    }
    .block {
        width: 300px;        
    }
    .vk {
        width: 200px;
        height: 60px;
    }
    .block p {
        display: none;
    }
    .ted {
        text-align: left;
        margin-left: 30px;
        font-size: 1rem;
    }
}

@media (max-width: 315px) {
    .gl {
        width: 250px;
        height: auto;
    }
    .block {
        width: 250px;        
    }
    .vk {
        width: 150px;
        height: 50px;
    }
}

